export default{
	data(){
		return{
			checkNum:1,
			money:998,
			isShow:false,
		}
	},
	methods:{
		changeNum(index){
			this.checkNum=index
			if(index==1||index==2){
				this.money=998
			}else{
				this.money=1888
			}
		},
		goPay(){
			//支付功能
			//取消支付跳转到待支付界面
			this.isShow=true
		},
		isKonw(){
			//前去待付款界面
			uni.navigateTo({
				url:"padingpayment"
			})
		}
	}
}
